//slider 
import demosliderImg from '@/assets/images/store-design/slider.jpg'
import singleImgDemo from '@/assets/images/store-design/single-img-demo.png'
//
import ceArr from "@/views/homePage/components/ce2.js";
Vue.use(Vuex)

export default new Vuex.Store({
  state:{
    userInfo: '',
    newMsgNum: 0,
    shopInfo: '',
    referenceNumber: '',
    categroyList: [],
    country:{
      symbol: '$',
      name:'',
      shopCurrency: '',
    },    
    configJson: { //
      detailPage: {
        imgSize: 'Thumbnail',
        tags: [],
      },
      menu:[],

		  customCode:[],
      customPage: [],
      header: { //
        logo: '',
        backgroundColor: '#fff',
        color: '#606266',
        isTransparent: false,
        transparentColor: '#606266',
        menu: [
          
        ], //1.6
        isVisibleSearch: true,
        activeMenu: '',
      },
      footer: { // 
        backgroundColor: '#fff',
        wordColor: '#606266',
        companyLogo: '',
        titleColor: '#606266',
        contentConfig: {
          scribeUsed: false,
          companyUsed: false,
          socalUsed: false,
          aboutUsed: false,
          navigateUsed: false
        },
        content: [
				],
				ourmall: true,
				footerWidth: '1200px',
      },
      sort: [
        { //
          cid: 111,
          type: 'slider',
          images: [
            // {
            //   id: 1,
            //   src: demosliderImg,
            //   width: 1920,
            //   height: 720,
            // },
            // {
            //   id: 2,
            //   src: demosliderImg,
            //   width: 1920,
            //   height: 720,
            // },
            // {
            //   id: 3,
            //   src: demosliderImg,
            //   width: 1920,
            //   height: 720,
            // },
          ],
          size: '1',
          auto: true,
        },
        { //
          cid: 112,
          type: 'richText',
          title: '',
          description: '',
          align: 'center',
          size: 'medium',
          width: 's' 
        },
        { //
          cid: 116,
          id:'',
          btnBackground:'#303133',
          btnColor:'#C0C4CC',
          type:'singleProduct',
          buttonText:'',//
          pictureRule:'left',//
          displayPrice:true,//
          name:'Product title',//
          cost:'88.88',// 
          imgUrl:'',
        },
        {
          cid: 120,
          type: 'singleImg',
          img: '',
          link: 'Products',
          height: 371,
          title: ' title',
          subTitle: 'subhead',
          size: 14,
          align: 'center',
          color: '#606266',
          btnText: 'button',
          btnColor: '#C0C4CC',
          btnBackground: '#303133',
        },
        {
          cid: 114,
          type: 'productCategory',
          title: '',
          rows: 2,
          cntPerRows: 3,
          moreText: '',
          proportion: '1',
          fillWay: 'cover',
          target: '_blank',
          categorys: []
        },
        {
          cid: 113,
          type:'hotProduct',
          productItems:[],
          isOpen:false,
          listTitle:'',
          moreText:'',
          displayPrice:true,
          rowNum:3,
          colNum:3,
          textAlign:'left'
        },
      ],
    },
		ceArr,
		activeCurrency: localStorage.getItem('c_activeCurrency') || "USD",  //
		activeLaguage:  "en", //
  },
  mutations: {
	getStoreComponent(state, obj) {
    if(!obj.detailPage){
      obj.detailPage = {
        imgSize: 'Thumbnail',
        tags: [],
      }
    }  
	    state.configJson = obj
	},
    setUserInfo(state, userInfo){
      userInfo.timeDiff = 0;
      if(userInfo.timestamp){
        userInfo.timeDiff = Date.parse(new Date()) / 1000 - userInfo.timestamp
      }
      state.userInfo = userInfo ? userInfo : '';
      // if (Tawk_API && Tawk_API.setAttributes && state.userInfo) {
      //   Tawk_API.setAttributes({
      //     name: state.userInfo.name,
      //     email: state.userInfo.email,
      //   }, function(error){});
      // }
      
    },
    setNewMsgNum(state, num) {
      state.newMsgNum = num;
    },
    setCountry(state,currency){
      state.country.symbol = currency.symbol
      state.country.shopCurrency = currency.shopCurrency
    },    
    setShopInfo(state, shopInfo){
      state.shopInfo = shopInfo ? shopInfo : '';
    },
    referenceNumber(state, referenceNumber){
      state.referenceNumber = referenceNumber ? referenceNumber : '';
    },    
    setCategroy(state, arr){
      state.categroyList = arr.map(e => e)
    },
		setLanguage(state, lang){
			state.activeLaguage = lang;
			localStorage.setItem('c_activeLaguage', lang)
		},
		setCurrency(state, ce){ //
			state.activeCurrency = ce;
			localStorage.setItem('c_activeCurrency', ce)
		},
		getRate(state, rate){
      if(rate && rate.length != 0){
        rate.forEach(r => {
				if(state.ceArr[r.name]){
					state.ceArr[r.name].rate = r.rate;
				}
			})
      }
			//
			if(!state.ceArr[state.activeCurrency].rate){
				state.activeCurrency = 'USD'
				localStorage.setItem('c_activeCurrency', 'USD')
			}
		},
  },
  actions: {
    
  }
})

